import {Injectable} from '@angular/core';

@Injectable()
export class Util {
  newGuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      // tslint:disable: no-bitwise
      const r = Math.random() * 16 | 0;
      const v = c === 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

  /**
   * åurlæ·»å QueryParam
   * @param url åå§url
   * @param paramName åæ°å
   * @param paramValue åæ°å¼
   * @param override æ¯å¦è¦çãä¸ºTrueæ¶ï¼å¦æåæ°å·²å­å¨ï¼å°è¦çå·²æåæ°ï¼å¦åæ¾å¼å¤çãé»è®¤ä¸ºFalseã
   */
  setUrlParam(url: string, paramName: string, paramValue: string, override?: boolean) {
    const [path, search] = url.split('?');
    const params = new URLSearchParams(search);
    if (params.has(paramName) && !override) {
      return url;
    }

    params.set(paramName, paramValue);
    return path + '?' + params.toString();
  }
}
